import React, { useState } from 'react';

import Error from './components/Error';
import Form from './components/Form';
import Success from './components/Success';
import Title from './components/Title';

import Container from '@material-ui/core/Container';

function App() {
  const [timezoneOffset, setTimezoneOffset] = useState('-05:00');
  const [startTime, setStartTime] = useState('');
  const [endTime, setEndTime] = useState('');

  const [isError, setIsError] = useState(false);
  const [errorText, setErrorText] = useState('');
  const [isSuccess, setIsSuccess] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();

    fetch('/merge', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        timezoneOffset: timezoneOffset,
        startTime: startTime,
        endTime: endTime,
      }),
    })
      .then((res) => {
        if (!res.ok) {
          setIsError(true);
          setIsSuccess(false);

          res.text()
            .then((text) => setErrorText(text));
        } else {
          setIsError(false);
          setIsSuccess(true);
        }
      })
      .catch((err) => console.error(err));
  };

  let errorComponent = null;
  if (isError) {
    errorComponent = ( <Error content={ errorText } /> );
  }

  let successComponent = null;
  if (isSuccess) {
    successComponent = ( <Success content='Points collected locally were merged successfully' /> );
  }

  return (
    <div>
      <Title />
      <Container maxWidth='sm'>
        { errorComponent }
        { successComponent }
        <Form
          timezoneOffset={ timezoneOffset }
          startTime={ startTime }
          endTime={ endTime }
          setTimezoneOffset={ setTimezoneOffset }
          setStartTime={ setStartTime }
          setEndTime={ setEndTime }
          handleSubmit={ handleSubmit }
        />
      </Container>
    </div>
  );
}

export default App;
